import React, { Component } from 'react'
export default class Todoinput extends Component {
    constructor(){
        super();
        this.state = {
            n1: '添加input'
        }
    }
    handleInput = (e)=>{
        if(e.keyCode === 13){
            this.props.addTodo(e.target.value);
        }
    }
    handleChange = (e)=>{
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    render() {
        return (
            <div>
                <label for="title">ToDoList</label>
                <input  name="n1" onChange={this.handleChange} placeholder='添加input' onKeyDown={(e)=>this.handleInput(e)} type="text"/>
            </div>
        )
    }
}