import React, { Component } from 'react';

export default class Todoing extends Component {
    constructor(){
        super();
    }
    change=(idx,item)=>{
        this.props.delTodo3(idx);
        this.props.addTodo3(item.name);
    }
    change2=(idx,item)=>{
        this.props.addTodo1(item.name);
        this.props.delTodo4(idx);
        
    }
    render() {
        return (
            <div>
                <div>
                    <h1>正在进行</h1>
                    <span>
                        {
                            this.props.all()
                        }
                    </span>
                    <ul>
                        {
                            this.props.todo.map((item,idx)=><li key={item}><input type="checkbox"  onChange={()=>{this.change(idx,item)}}/>{item.name}----<button onClick={()=>{
                                this.props.delTodo(item,idx)
                            }}>删除</button></li>)
                        }
                    </ul>
                </div>
                <h1>已经完成</h1>
                <span>
                        {
                            this.props.all2()
                        }
                    </span>
                <ul>
                        {
                            this.props.todo2.map((item,idx)=><li key={item}><input type="checkbox" onChange={()=>{this.change2(idx,item)}}/>{item.name}----<button onClick={()=>{
                                this.props.delTodo2(item,idx)
                            }}>完成</button></li>)
                        }
                    </ul>
            </div>
            
        )
    }
}