import React, { Component } from 'react'
import Todoing from './Todoing';
import Todoinput from './Todoinput';
var i = 0;
for (let index = 0; index < localStorage.length; index++) {
    var key = localStorage.key(index);
    if(key>i){
        i=key;
    }
}
console.log(i);
export default class Todolist extends Component {
    constructor(){
        var todo = [];
        var todo2 = [];
        for (let index = 0; index < localStorage.length; index++) {
            var key = localStorage.key(index);
            var a = JSON.parse(localStorage.getItem(key));
            if(a.isDoing === false){
                todo.push({"name":a.name,"isDoing":a.isDoing});
            }else{
                todo2.push({"name":a.name,"isDoing":a.isDoing});
            }
        }
        super();
        this.state = {
            todo:todo,
            todo2:todo2
        }
        
    }
    addItem = (msg)=>{
        i++;
        var addMsg = {"name":msg,"isDoing":false};
        this.setState({
            todo:[...this.state.todo,addMsg]
        },()=>{
            addMsg = JSON.stringify(addMsg);
            localStorage.setItem(i,addMsg);
        })
    }
    addItem2 = (msg)=>{
        i++;
        var addMsg = {"name":msg,"isDoing":false};
        this.setState({
            todo2:[...this.state.todo2,addMsg]
        },()=>{
            addMsg = JSON.stringify(addMsg)
            localStorage.setItem(i,addMsg);
        })
    }
    addItem1 = (msg)=>{
        var add = {"name":msg,"isDoing":true};
        this.setState({
            todo: [...this.state.todo,add]
        })
        for (let index = 0; index <= i; index++) {
            var addMsg = JSON.stringify(add);
            if(addMsg==localStorage.getItem(index)){
                add.isDoing = false;
                var addMsg1 = JSON.stringify(add);
                localStorage.removeItem(index);
                localStorage.setItem(index,addMsg1);
            }
        }
    }
    addItem3 = (msg)=>{
        var add = {"name":msg,"isDoing":false};
        this.setState({
            todo2: [...this.state.todo2,add]
        })
        for (let index = 0; index <= i; index++) {
            var addMsg = JSON.stringify(add);
            if(addMsg==localStorage.getItem(index)){
                add.isDoing = true;
                var addMsg1 = JSON.stringify(add);
                localStorage.removeItem(index);
                localStorage.setItem(index,addMsg1);
            }
        }
    }
    
    delItem = (item)=>{
        var todo = [...this.state.todo];
        for (let index = 0; index <= i; index++) {
            var add = JSON.stringify(item);
            if(add==localStorage.getItem(index)){
                localStorage.removeItem(index);
            }
        }
        for (let index = 0; index < todo.length; index++) {
            if(todo[index] == item){
                todo.splice(index,1);
            }
        }
        this.setState({
            todo
        })
    }
    delItem3 = (item)=>{
        var todo = [...this.state.todo];
        todo.splice(item,1);
        this.setState({
            todo
        })
    }
    delItem4 = (item)=>{
        var todo2 = [...this.state.todo2];
        todo2.splice(item,1);
        this.setState({
            todo2
        })
    }
    
    delItem2 = (item)=>{
        var todo2 = [...this.state.todo2];
        for (let index = 0; index <= i; index++) {
            var add = JSON.stringify(item);
            if(add==localStorage.getItem(index)){
                localStorage.removeItem(index);
            }
        }
        for (let index = 0; index < todo2.length; index++) {
            if(todo2[index] == item){
                todo2.splice(index,1);
            }
            
        }
        this.setState({
            todo2
        })
        
    }
    all=()=>{
        var todo = [...this.state.todo];
        var a = 0;
        for (let index = 0; index < todo.length; index++) {
            a++;
        }
        return a;
    }
    all2=()=>{
        var todo2 = [...this.state.todo2];
        var a = 0;
        for (let index = 0; index < todo2.length; index++) {
            a++;
        }
        
        return a;
    }
    render() {
        return (
            <div>
                <Todoinput addTodo={this.addItem}/>
                <Todoing all2={this.all2} all={this.all} addTodo={this.addItem} addTodo2={this.addItem2} addTodo1={this.addItem1} addTodo3={this.addItem3} delTodo={this.delItem} delTodo4={this.delItem4} delTodo3={this.delItem3} delTodo2={this.delItem2} todo2={this.state.todo2} todo={this.state.todo}/>
            </div>
        )
    }

    
}